.ease {
    padding: 100px 0;
    position: relative;


    @media (max-width: $bp-tablet) {
        padding: 0 0 65px;
    }

    &-header {
        margin-bottom: 91px;
        text-align: center;

        @media (max-width: $bp-tablet) {
            margin-bottom: 65px;
        }

        h3 {
            font-size: 16px;
            line-height: normal;
            letter-spacing: 0;
            max-width: 167px;
            padding: 2px;
            margin: 0 auto 50px;
            font-weight: 700;
        }

        h2 {
            margin-bottom: 45px;
        }
    }




    &-item {
        text-align: left;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 30px;
        min-height: 209px;
        box-shadow: $shadow-box;
        max-width: 460px;
        margin: 0 auto;

        @media (max-width: $bp-tablet) {
            max-width: none;
            margin: 0 auto 30px;
            text-align: center;
            flex-direction: column;
            gap: 10px;
        }


        &__content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 19px;
            max-width: 309px;

            @media (max-width: $bp-tablet) {
                max-width: none;

            }
        }





        &__image {
            margin-bottom: 34px;

            @media (max-width: $bp-tablet) {
                margin-bottom: 0;
            }
        }

        p {
            font-size: 16px;

            strong {
                font-size: 18px;
                margin-bottom: 19px;
            }
        }
    }
}