.future {
    padding: 100px 0 150px;
    position: relative;

    @media (max-width: $bp-tablet) {
        padding: 120px 0;
    }


    &-header {
        margin-bottom: 91px;
        text-align: center;

        @media (max-width: $bp-tablet) {
            margin-bottom: 65px;
        }

        h2 {
            margin-bottom: 34px;
        }

        h3 {
            font-size: 16px;
            line-height: normal;
            letter-spacing: 0;
            max-width: 167px;
            padding: 2px;
            margin: 0 auto 50px;
            font-weight: 700;
        }
    }




    &-item {
        text-align: left;

        @media (max-width: $bp-tablet) {
            max-width: 300px;
            margin: 0 auto 30px;
            text-align: center;
        }

        &__image {
            margin-bottom: 34px;
        }

        p {
            font-size: 16px;

            strong {
                font-size: 18px;
                margin-bottom: 19px;
            }
        }
    }
}