* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", system-ui, sans-serif;
  color: #000000;
  position: relative;
  font-size: 1rem;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  max-width: 1510px;
  width: 100%;
  margin: 0 auto;
  padding: 0 1.5rem;
}

h1,
h2,
h3 {
  font-weight: 700;
}

h2 {
  font-size: 3.375rem;
  line-height: 60px;
  letter-spacing: 0;
  text-align: center;
  margin: 0;
}
@media (max-width: 768px) {
  h2 {
    font-size: 30px;
    line-height: 36px;
  }
}

h3 {
  font-size: 1rem;
  line-height: 38px;
  letter-spacing: 0;
}

img {
  width: 100%;
  height: auto;
}

.bg-green {
